<template>
  <div class="cart">
    <div class="list mescroll" id="list">
      <div class="list-data" id="listData">
        <div class="item" v-for="(item,index) in list" :key="index">
          <van-swipe-cell :right-width="65">
            <div class="flex" style="margin-right:10px;height:120px;">
              <div class="choose" @click="clickIcon(item)" :style="item.choose?'border-color:#fff':''">
                <img v-if="item.choose" src="../../assets/img/icon/gou.png" />
              </div>
              <div class="item-pic">
                <img :src="$utils.dealImg(item.scEcomGoods.gimg)" />
              </div>
              <div class="flex-2">
                <div class="title">{{item.scEcomGoods.gname}}</div>
                <!-- <div class="parameter">{{item.platform_goods_spec_value_names}}</div> -->
                <div style="margin-top:20px;align-items:center;" class="flex">
                  <div class="price">￥{{item.scEcomGoods.goutPrice?item.scEcomGoods.goutPrice.toFixed(2):'0.00'}}</div>
                  <div class="stepper">
                    <img class="subtract" @click="subtract(item)" src="../../assets/img/icon/jian.png" alt />
                    <input class="input" type="number" v-model="item.gOrderCount" @input="onInput(item)" @blur="blur" />
                    <img class="add" @click="add(item)" src="../../assets/img/icon/jia.png" alt />
                  </div>
                </div>
              </div>
            </div>
            <div slot="right" class="del" @click="del(item.detailCode)">移除</div>
          </van-swipe-cell>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="left">
        <div class="choose" @click="clickAll" :style="chooseAll?'border-color:#fff':''">
          <img v-if="chooseAll" src="../../assets/img/icon/gou.png" />
        </div>
        <p>全选</p>
      </div>

      <div class="mid">
        总计：
        <span class="font">¥</span>
        <span class="price">{{allPrice.toFixed(2)}}</span>
      </div>
      <div class="btn" @click="settle">
        <span>去结算</span>
        <span v-if="num">({{num}}件)</span>
      </div>
    </div>
  </div>
</template>
<script>
import { SwipeCell, SwitchCell, Dialog } from "vant";
import { getCartList, reviseCartNumber, deleteCart } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [SwipeCell.name]: SwipeCell,
    [SwitchCell.name]: SwitchCell,
    [Dialog.name]: Dialog
  },
  data() {
    return {
      value: 1,
      chooseAll: false,
      list: [],
      num: 0,
      allPrice: 0,
      id: ""
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    revise(num, id) {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      reviseCartNumber({
        id: id,
        shopNum: num
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("修改成功");
          this.calculate();
        }
      });
    },
    del(id) {
      let that = this;
      Dialog.confirm({
        message: "确定删除吗？"
      })
        .then(() => {
          deleteCart({
            id: id
          }).then(res => {
            if (res.code == 0) {
              that.$toast("删除成功");
              that.mescroll.resetUpScroll();
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    clickIcon(item) {
      item.choose = !item.choose;
      let flag = true,
        num = 0;
      this.list.map(item1 => {
        if (!item1.choose) {
          flag = false;
        } else {
          num++;
        }
      });
      this.chooseAll = flag;
      this.num = num;
      this.calculate();
    },
    calculate() {
      let allPrice = 0;
      this.list.map(item => {
        if (item.choose) {
          allPrice += item.gOrderCount * item.scEcomGoods.goutPrice;
        }
      });
      this.allPrice = allPrice;
    },
    subtract(item) {
      if (item.gOrderCount <= 1) {
        this.$toast("数量不能少于1");
        return;
      }
      item.gOrderCount -= 1;
      this.revise(item.gOrderCount, item.detailCode);
    },
    onInput(item) {
      console.log(item);
      if (item.gOrderCount < 1) {
        this.$toast("数量不能少于1");
        item.gOrderCount = 1;
      }
      item.gOrderCount = parseInt(item.gOrderCount);
      this.revise(item.gOrderCount, item.detailCode);
    },
    add(item) {
      item.gOrderCount += 1;
      this.revise(item.gOrderCount, item.detailCode);
    },
    clickAll() {
      if (!this.list.length) {
        return;
      }
      this.chooseAll = !this.chooseAll;
      this.list.map(item => {
        item.choose = this.chooseAll;
      });
      if (this.chooseAll) {
        this.num = this.list.length;
      } else {
        this.num = 0;
      }
      this.calculate();
    },
    settle() {
      let flag = false,
        cartIds = [],
        data = [];
      this.list.map(item => {
        if (item.choose) {
          flag = true;
          cartIds.push(item.detailCode);
          data.push({
            cartId: item.detailCode,
            id: item.scEcomGoods.gcode,
            gname: item.scEcomGoods.gname,
            gimg: item.scEcomGoods.gimg,
            goutPrice: item.scEcomGoods.goutPrice,
            num: item.gOrderCount
          });
        }
      });
      if (!flag) {
        this.$toast("请选择结算商品");
        return;
      }
      sessionStorage.setItem("goodsList", JSON.stringify(data));
      this.$router.push({
        name: "confirmOrder",
        query: { type: "2" }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize
      };
      getCartList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              item.choose = false;
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "购物车";
    sessionStorage.removeItem("address");
    sessionStorage.removeItem("goodsList");
    sessionStorage.removeItem("couponDetail");
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.cart {
  height: calc(100vh - 100px);
  .del {
    width: 10px;
    margin: 40px auto 0;
  }
  .list {
    border-bottom: 1pt solid #efefef;
    .item {
      overflow: hidden;
      padding: 10px;
      border-top: 1pt solid #efefef;
      .flex {
        display: flex;
        align-items: center;
      }
      .flex-1 {
        flex: 1;
      }
      .flex-2 {
        flex: 2;
      }
      .choose {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin-right: 10px;
        border: 1pt solid #e0e0e0;
        img {
          width: 100%;
        }
      }

      .item-pic {
        margin-right: 10px;
        width: 100px;
        img {
          width: 100%;
        }
      }
      .title {
        font-size: 16px;
        line-height: 25px;
        max-height: 50px;
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
      }
      .parameter {
        color: #999;
        font-size: 15px;
        line-height: 20px;
        margin: 10px 0;
      }
      .price {
        font-size: 15px;
        font-weight: 600;
        color: #eb5252;
        flex: 2;
      }
      .stepper {
        display: flex;
        align-items: center;
        .subtract {
          width: 25px;
          height: 25px;
        }
        .input {
          width: 40px;
          height: 25px;
          border: 0;
          text-align: center;
          line-height: 25px;
        }
        .add {
          width: 25px;
          height: 25px;
        }
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 50px;
    left: 0;
    width: calc(100% - 10px);
    background: #fff;
    padding: 0 0 0 10px;
    display: flex;
    align-items: center;
    height: 50px;
    border-top: 1pt solid #efefef;

    .left {
      flex: 1;
      display: flex;
      align-items: center;
      .choose {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin-right: 10px;
        border: 1pt solid #e0e0e0;
        img {
          width: 100%;
        }
      }
    }

    .mid {
      line-height: 50px;
      flex: 2;
      text-align: right;
      margin-right: 10px;
      .font {
        color: #eb5252;
        font-size: 13px;
        margin-right: 3px;
      }
      .price {
        color: #eb5252;
        font-size: 18px;
        font-weight: bold;
      }
    }
    .btn {
      line-height: 50px;
      width: 120px;
      background: #eb5252;
      color: #fff;
      text-align: center;
      font-size: 13px;
    }
  }
}
</style>
<style lang="scss">
.cart {
  .van-swipe-cell__right {
    background: #eb5252;
    color: #fff;
    height: 119px;
    text-align: center;
    width: 65px;
    font-size: 15px;
  }
}
</style>
